:root {
  --sirkaStlpcov: 25px;
  --fontSizeRed: 12px;
  --fontSize: 15px;
}

.svkGraf{
  position: relative;
  height: 50vh;
  display: flex;
  align-items: flex-end;
  justify-content: space-around;
  border-bottom: 1px solid black;
  width: 94%;
  margin-left: 3%;
  margin-top: 15px;
  /* justify-content: center; */
}

.covidApp{
  padding-bottom: 1px;
  background: rgba(243, 243, 243, 0.411);
  min-height: 90vh;
}

.outsideDivGraf{
  background: white;
  border-radius: 10px;
  width: 80%;
  margin-left: 10%;
  margin-top: 80px;
  -webkit-box-shadow: 5px 5px 24px -26px rgba(0,0,0,0.75);
  -moz-box-shadow: 5px 5px 24px -26px rgba(0,0,0,0.75);
  box-shadow: 5px 5px 24px -26px rgba(0,0,0,0.75);
  margin-bottom: 250px;
  padding-right: 25px;
  padding-top: 5px;
  position: relative;
}

.outsideDivGraf h1{
  /* margin-top: -30px; */
  /* color: red; */
  font-size: 1.5rem;
  color: rgb(77, 77, 77);
  margin-bottom: 50px;
}


.stlpcePrvehoGrafu{
  background-image: linear-gradient(to right, #e0908a 0%, #ff3030 100%);
  display: flex;
  align-self: flex-end;
  width:  var(--sirkaStlpcov);
  transition:  .4s;
  position: relative;
}

.aktivnePripadyGraf{
  /* background-image: linear-gradient(to right, #e0908a 0%, #ff3030 100%); */
  /* background-image: linear-gradient(to top, #48c6ef 0%, #6f86d6 100%); */
  background: linear-gradient(to top, #3497b8 0%, #2e469e 100%);
  display: flex;
  align-self: flex-end;
  width:  var(--sirkaStlpcov);
  position: relative;
  /* animation:stlpceFarba 4s linear infinite; */
  /* background: red; */
}

